import { ApodService } from './apod.service';
import { Component, OnDestroy, OnInit } from '@angular/core';
import { Observable, Subscription } from 'rxjs';
import { IApod } from './apod';

@Component({
  selector: 'app-apod',
  templateUrl: './apod.component.html',
  styleUrls: ['./apod.component.css']
})
export class ApodComponent implements OnInit, OnDestroy {

  pageTitle = 'Picture of the Day';
  apod!: IApod;
  public apod$: Observable<IApod> | undefined;

  private _subscription: Subscription = new Subscription;
  errorMessage = '';

  constructor(private apodService: ApodService) { }

  ngOnInit(): void {
    //this.apodService.getByDate(date).subscribe(updatedApod => this.apod = updatedApod);
    this._subscription = this.apodService.get().subscribe(
      {
        next: Apod => this.apod = Apod,
        error: error => this.errorMessage = error
      }
    );
    console.log('apod component on init');

  }

  onDateChange(date: string): void {
    console.log('change date ' + date);
    this.apodService.getByDate(date);
  }

   ngOnDestroy(): void {
     this._subscription.unsubscribe();
   }

}
